




















import { Vue, Component, Prop } from 'vue-property-decorator';

@Component
export default class Guideline extends Vue {
    @Prop()
    public guideline!: any;

    @Prop()
    public category!: any;

    get tags() {
        return this.guideline.tags.filter((tags: any) =>
            tags.includes(this.category) ? false : true,
        );
    }

    get overdue() {
        const dateParts = this.guideline.review_date.split('/', 3);
        const reviewDate = new Date(dateParts[2], dateParts[1], dateParts[0]);
        return reviewDate.getTime() <= Date.now();
    }

    public view(guidelineKey: string) {
        this.$router.push({ name: 'viewer', params: { guidelineKey } });
    }
}
