





























import { Vue, Component, Prop } from 'vue-property-decorator';

@Component
export default class Viewer extends Vue {
    @Prop()
    public guidelineKey!: any;

    public created() {
        if (this.guidelineKey == null) {
            this.$router.replace({ name: 'home' });
        }
    }

    get guideline() {
        return (this.$parent as any).guidelines[this.guidelineKey];
    }

    get pdf() {
        return (
            this.guideline.file.substring(
                this.guideline.file.length - 4,
                this.guideline.file.length,
            ) === '.pdf'
        );
    }

    public pdfViewer() {
        const plugin = (navigator.mimeTypes as any)['application/pdf'];
        return plugin && plugin.enabledPlugin;
    }
}
