














































































































































import { Component, Vue, Watch } from 'vue-property-decorator';
import Home from '@/views/Home.vue';

import YAML from 'yamljs';
import CSV from 'comma-separated-values';
import Fuse from 'fuse.js';

Component.registerHooks(['beforeRouteEnter']);

@Component({
    components: {
        Home,
    },
})
export default class Organisation extends Vue {
    public title: string = 'Guidelines';
    public desc: string = '';
    public guidelineCsv: object = {};
    public categoriesYaml: object = {};
    public categoriesCsv: object = {};
    public links: object = {};
    public protectedEndpoint: string = '';
    public protectedCsv: string = '';

    public rawCategories: object = {};
    public guidelines: object = {};
    public index: any = null;

    public query: string = '';
    public results: object = {};

    public loading: boolean = true;
    public sidebar: boolean = false;

    public unlock: boolean = false;
    public unlockState: number = 0;
    public unlockPIN: string = '';

    public beforeRouteEnter(to: any, from: any, next: any) {
        for (const el of document.querySelectorAll('[data-dynamic]')) {
            if (el.parentNode != null) {
                el.parentNode.removeChild(el);
            }
        }
        const tag = document.createElement('link');
        tag.setAttribute('rel', 'manifest');
        tag.setAttribute(
            'href',
            '/' + to.params.organisation + '/manifest.json',
        );
        tag.setAttribute('data-dynamic', '');
        document.head.appendChild(tag);

        const xhr = new XMLHttpRequest();
        xhr.open('GET', '/' + to.params.organisation + '/config.yaml');
        xhr.onload = () => {
            const data = YAML.parse(xhr.responseText);
            if (typeof data === 'object') {
                next((vm: any) => vm.loadConfig(data));
            } else {
                next({ replace: true, name: 'root' });
            }
        };
        xhr.send();
    }

    get categories() {
        if (this.unlock) {
            return this.rawCategories;
        }
        const output: any = {};
        for (const categoryKey of Object.keys(this.rawCategories)) {
            const category = (this.rawCategories as any)[categoryKey];
            if (!category.protectedFlag) {
                output[categoryKey] = Object.assign({}, category);
                if (output[categoryKey].subcategories != null) {
                    output[categoryKey].subcategories = output[
                        categoryKey
                    ].subcategories.filter((subcategory: any) =>
                        subcategory.protectedFlag ? false : true,
                    );
                }
            }
        }
        return output;
    }

    get online() {
        if (typeof window !== 'undefined') {
            return navigator.onLine ? true : false;
        }
        return false;
    }

    @Watch('$route')
    public onRouteChange() {
        this.sidebar = false;
    }

    @Watch('query')
    public onQueryChange(val: string) {
        if (val === '') {
            this.results = {};
            return;
        }
        this.results = this.index.search(val);
    }

    @Watch('guidelineCsv')
    public onGuidelineCsvChange(val: any) {
        const self = this;
        this.fetchCsv(val, false, (data: object) => {
            self.guidelines = data;
            self.buildIndex();
            self.loading = false;
        });
    }

    @Watch('protectedCsv')
    public onProtectedCsvChange(val: any) {
        const self = this;
        this.fetchCsv(val, true, (data: object[]) => {
            data = data.concat(self.guidelines);
            data.sort((a: any, b: any) => {
                const x = a.name.toLowerCase();
                const y = b.name.toLowerCase();
                if (x < y) {
                    return -1;
                }
                if (x > y) {
                    return 1;
                }
                return 0;
            });
            for (const key of Object.keys(data)) {
                (data as any)[key].key = key;
            }
            self.guidelines = data;
            self.buildIndex();
            self.unlock = true;
        });
    }

    @Watch('categoriesYaml')
    public onCategoriesYamlChange(url: any) {
        const self = this;
        const xhr = new XMLHttpRequest();
        xhr.open('GET', url);
        xhr.onload = () => {
            const output: any = {};
            for (const category of YAML.parse(xhr.responseText)) {
                output[category.tag] = category;
            }
            self.rawCategories = output;
        };
        xhr.send();
    }

    @Watch('categoriesCsv')
    public onCategoriesCsvChange(url: any) {
        const self = this;
        let xhr: any;
        if (typeof XDomainRequest !== 'undefined') {
            // Assume the browser does not support XMLHttpRequest with CORS
            // eslint-disable-next-line
            xhr = new XDomainRequest();
        } else {
            xhr = new XMLHttpRequest();
        }
        xhr.open('GET', url);
        xhr.onload = () => {
            const output: any = {};
            const categories = new CSV(xhr.responseText, {
                header: true,
                cast: false,
            }).parse();
            for (const key of Object.keys(categories)) {
                const csvLine = categories[key];
                if (!csvLine.parent_tag) {
                    if (!(csvLine.tag in output)) {
                        output[csvLine.tag] = { subcategories: [] };
                    }
                    output[csvLine.tag].title = csvLine.title;
                    output[csvLine.tag].tag = csvLine.tag;
                    output[csvLine.tag].desc = csvLine.description;
                    output[csvLine.tag].protectedFlag =
                        csvLine.protected_flag === 'TRUE' ? true : false;
                } else {
                    if (!(csvLine.parent_tag in output)) {
                        output[csvLine.parent_tag] = {
                            title: 'orphan',
                            subcategories: [],
                        };
                    }
                    output[csvLine.parent_tag].subcategories.push({
                        title: csvLine.title,
                        tag: csvLine.tag,
                        desc: csvLine.description,
                        protectedFlag:
                            csvLine.protected_flag === 'TRUE' ? true : false,
                    });
                }
            }
            for (const key of Object.keys(output)) {
                if (output[key].title === 'orphan') {
                    delete output[key];
                }
            }
            self.rawCategories = output;
        };
        xhr.send();
    }

    public loadConfig(data: any) {
        this.title = document.title = data.title;
        this.desc = data.desc;
        this.guidelineCsv = data.guidelines;

        const dataCategories = data.categories as string;
        const categoriesExt = dataCategories.substring(
            dataCategories.length - 4,
            dataCategories.length,
        );
        if (categoriesExt === 'yaml') {
            this.categoriesYaml = data.categories;
        } else {
            this.categoriesCsv = data.categories;
        }

        this.links = data.links;
        this.protectedEndpoint = data.protected as string;
    }

    public fetchCsv(
        url: string,
        protectedFlag: boolean,
        data: (data: object[]) => void,
    ) {
        let xhr: any;
        if (typeof XDomainRequest !== 'undefined') {
            // Assume the browser does not support XMLHttpRequest with CORS
            // eslint-disable-next-line
            xhr = new XDomainRequest();
        } else {
            xhr = new XMLHttpRequest();
        }
        xhr.open('GET', url);
        xhr.onload = () => {
            const output = [];
            const records = new CSV(xhr.responseText, { header: true }).parse();
            records.sort((a: any, b: any) => {
                const x = a.name.toLowerCase();
                const y = b.name.toLowerCase();
                if (x < y) {
                    return -1;
                }
                if (x > y) {
                    return 1;
                }
                return 0;
            });
            for (const key of Object.keys(records)) {
                const record = records[key];
                const guideline = {
                    key,
                    name: record.name,
                    tags: record.tags
                        .split(',')
                        .map((item: string) => item.trim()),
                    description: record.description,
                    version: record.version,
                    review_date: record.review_date,
                    file: record.file,
                    source_link: record.source_link,
                    protectedFlag,
                };

                output.push(guideline);
            }

            data(output);
        };
        xhr.send();
    }

    public buildIndex() {
        const self: any = this;
        const options: Fuse.FuseOptions<any> = {
            shouldSort: true,
            tokenize: true,
            matchAllTokens: true,
            findAllMatches: true,
            threshold: 0.3,
            minMatchCharLength: 3,
            keys: ['name', 'tags', 'description'],
        };
        this.index = new Fuse(self.guidelines, options);
    }

    public view(guidelineKey: string) {
        this.$router.replace({ name: 'home' });
        this.$router.push({ name: 'viewer', params: { guidelineKey } });
    }

    public getProtected(e: any) {
        e.preventDefault();
        this.unlockState = 3;
        let xhr: any;
        if (typeof XDomainRequest !== 'undefined') {
            // Assume the browser does not support XMLHttpRequest with CORS
            // eslint-disable-next-line
            xhr = new XDomainRequest();
        } else {
            xhr = new XMLHttpRequest();
        }
        xhr.open('GET', this.protectedEndpoint + '&pin=' + this.unlockPIN);
        xhr.onreadystatechange = () => {
            if (xhr.readyState === XMLHttpRequest.DONE && xhr.status === 200) {
                const response = JSON.parse(xhr.responseText);
                if (response.error === true) {
                    this.unlockPIN = '';
                    this.unlockState = 4;
                } else {
                    this.protectedCsv = response.csv as string;
                }
            } else if (xhr.readyState === XMLHttpRequest.DONE) {
                this.unlockPIN = '';
                this.unlockState = 4;
            }
        };
        xhr.send();
    }
}
