





































import { Vue, Component } from 'vue-property-decorator';
import Guideline from '@/components/Guideline.vue';

@Component({
    components: {
        Guideline,
    },
})
export default class Guidelines extends Vue {
    public beforeCreate() {
        if (
            !(this.$route.params.category in (this.$parent as any).categories)
        ) {
            this.$router.replace({ name: 'home' });
        }
    }

    get category() {
        return (this.$parent as any).categories[this.$route.params.category];
    }

    get guidelines() {
        const guidelines = (this.$parent as any).guidelines;
        return guidelines.filter((guideline: any) =>
            guideline.tags.includes(this.category.tag),
        );
    }

    get guidelinesFiltered() {
        let guidelines = this.guidelines;
        if (this.category.subcategories) {
            for (const subcategory of this.category.subcategories) {
                guidelines = guidelines.filter((guideline: any) =>
                    guideline.tags.includes(subcategory.tag) ? false : true,
                );
            }
        }
        return guidelines;
    }

    get guidelinesByTag() {
        const guidelineMap: any = {};
        for (const subcategory of this.category.subcategories) {
            guidelineMap[subcategory.tag] = this.guidelines.filter(
                (guideline: any) => guideline.tags.includes(subcategory.tag),
            );
        }
        return guidelineMap;
    }
}
